.Mnavbar {
    display: flex;
    justify-content: center;
    background-color: #EAEAEA;
    margin-top: 6px;
  }
  
  .Mnavbar button {
    padding: 12px 50px;
    border: none;
    background-color: transparent;
    font-size: 22px;
    font-family: 'Alata', Bold;
    color: #333333;
    cursor: pointer;
    border-radius: 40px;
    margin: 10px;
  }
  
  .Mnavbar button:hover {
    background-color: #d4d4d4;
  }
  