.diary-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .diary-textarea-wrapper {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 60%;
    height: 800px;
    border-radius: 20px;
    overflow: hidden;
    background-color: #f6f5f5;
    margin-top: 10px;
    margin-bottom: 50px;
  }
  
  .title-date-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 60px 10px 5px;
    box-sizing: border-box;
    z-index: 1;
    position: absolute;
    top: 0;
    background-color: #f6f5f5;
  }
  
  .date {
    font-size: 21px;
    font-family: 'Alata';
    color: #251c1c;
    margin-left: 5px;
  }
  
  .date-container {
    position: absolute;
    top: 10px;
    left: 10px;
    display: flex;
    align-items: center;
    padding: 9px;
    background-color: #EAEAEA;
    border-radius: 11px;
    width: 130px;
    z-index: 2;
  }
  
  .diary-title {
    width: 100%;
    font-family: 'Alata', Regular;
    font-size: 32px;
    border: none;
    border-bottom: 1px solid #dddddd;
    font-weight: bold;
    outline: none;
    padding: 5px 10px;
    margin-bottom: 10px;
    background-color: #f6f5f5;
  }
  
  .diary-textarea {
    width: 100%;
    height: 700px;
    overflow-y: auto;
    resize: none;
    line-height: 35px;
    padding: 125px 18px 10px 18px;
    font-family: 'Alata', Regular;
    font-size: 28px;
    border: none;
    outline: none;
    background-color: #f6f5f5;
    margin-top: 11px;
  }
  
  .diary-page h1 {
    font-size: 3.5rem;
    margin-top: 40px;
  }
  
  /* Hide the save-button as it's not needed in this page */
  .save-button {
    display: none;
  }
  